import request from '@/utils/request.js'
// 
export function getTopAndSecondProjec() {
	return request({
		url: 'api/study/getTopAndSecondProjec',
		method: 'GET',
	})
}

export function getSecondProjectByName(data) {
	return request({
		url: 'api/study/getSecondProjectByName',
		method: 'GET',
		data: data
	})
}

